import React from 'react'
import CardSec3 from "./CardSec3";
import unsplash5 from "../images/unsplash5.jpg";
import unsplash6 from "../images/unsplash6.jpg";
import unsplash7 from "../images/unsplash7.jpg";
import unsplash8 from "../images/unsplash8.jpg";

function Section3() {
  return (
    <div className="section3">
      <div className="secLine">
        <div></div>
        <div className="sectLine_text"><h2>POPULAR PACKAGES</h2></div>
        <div></div>
      </div>
      <div className="d-flex">
        < CardSec3 img={unsplash5}/>
        < CardSec3 img={unsplash6}/>
      </div>
      <div className="d-flex">
        < CardSec3 img={unsplash7}/>
        < CardSec3 img={unsplash8}/>
      </div>   
    </div>
  )
}

export default Section3
